.avaOpenImg {
  width: 80%;
}
.avaOpenDiv2 {
  border-radius: 5%;
  cursor: auto;
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--bg-color);
  color: var(--text-color);
  border: var(--border05);
  height: 600px;
}
.avaOpenDiv {
  z-index: 1;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
}
.exitImg {
  width: 40px;
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  transition: transform 0.2s;
}
.exitImg:hover {
  transform: scale(1.4);
}
.origImgLink {
  text-decoration: none;
  color: var(--text-color);
  font-size: 20px;
  font-weight: 700;
  transition: filter 0.2s;

}
.origImgLink:hover{
  filter: drop-shadow(0px 1px 7px rgba(255, 255, 255, 1));

}